<script>
import Sidebar from "@/components/Sidebar.vue";
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
import Mobile from "../components/Mobile.vue";
export default {
  components:{
    Mobile,
    Sidebar,
  },
  data(){
    return{
      data:[],
      isActive:0,
      isSend:0,
      price:'',
      telegram_username:'',
      isCheck:false,
      data1:[],
      coin:''
    }
  },
  mounted() {
    this.$http.get('/add-funds/',{
      headers:{
        Authorization:`Bearer ${localStorage.getItem('token')}`
      }
    })
        .then(res=>{
          this.data=res.data
        })
    this.$http.get('/coins/',{
      headers:{
        Authorization:`Bearer ${localStorage.getItem('token')}`
      }
    })
        .then(res=>{
          this.data1=res.data
        })
  },
  methods:{
    send(){
      this.isCheck=true
      this.$http.post('/send-transaction/',{
          "coin":this.coin,
          "amount":this.price,
          "recipientTgUsername":this.telegram_username

      },
          {
        headers:{
          Authorization:`Bearer ${localStorage.getItem('token')}`
        }
      })
          .then(res=>{
            this.data=res.data
            this.isCheck=false
            this.$toast(`Success`, {
              timeout: 2000,
              type:'success'
            });

          })
          .catch(error=>{
            this.isCheck=false
            this.$toast(`${error.response.data}`, {
              timeout: 2000,
              type:'error'
            });
          })
    },
    send1(){
      this.$http.post('/receive-transaction/',{
            "coin":this.coin,
            "amount":this.price,
            "recipientTgUsername":this.telegram_username

          },
          {
            headers:{
              Authorization:`Bearer ${localStorage.getItem('token')}`
            }
          })
          .then(res=>{
            this.data=res.data
            this.data=res.data
            this.isCheck=false
            this.$toast(`Success`, {
              timeout: 2000,
              type:'success'
            });
          })
          .catch(error=>{
            this.isCheck=false
            this.$toast(`${error.response.data}`, {
              timeout: 2000,
              type:'error'
            });
          })
    }
  }
}
</script>
