<script>
export default {
  data(){
    return{
      firstName:'',
      lastName:'',
      email:'',
      telegramUsername:'',
      password:'',
      confirmPassword:'',
      isCheck:false
    }
  },
  methods:{
    sign_up(){
      this.isCheck=true
      this.$http.post('signup/',{
        firstName:this.firstName,
        lastName:this.lastName,
        email:this.email,
        telegramUsername:this.telegramUsername,
        password:this.password,
        confirmPassword:this.confirmPassword
      })
      .then(res=>{
        localStorage.setItem('token',res.data.access)
        this.$router.push('/')
        this.isCheck=false
      })
      .catch(error=>{
        console.log(error.response.data)
        if (error.response) {
          // The request was made and the server responded with a status code
          // that falls out of the range of 2xx
          this.$toast(`${error.response.data.confirmPassword}`, {
            timeout: 2000,
            type:'error'
          });
          this.$toast(`${error.response.data.email}`, {
            timeout: 2000,
            type:'error'
          });
          this.$toast(`${error.response.data.firstName}`, {
            timeout: 2000,
            type:'error'
          });
        }
        else if (error.request) {
          this.$toast(`${error.response.data.lastName}`, {
            timeout: 2000,
            type:'error'
          });
        }
        else {
          this.$toast(`${error.message}`, {
            timeout: 2000,
            type:'error'
          });
        }
        this.$toast(`${error.config}`, {
          timeout: 2000,
          type:'error'
        });

        this.isCheck=false
      })
    }
  }
}
</script>
